const searchData = (
  objList: ClaimToPrintInterface[],
  text: string
): ClaimToPrintInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "names" ||
        prop === "gender" ||
        prop === "national_id" ||
        prop === "phone" ||
        prop === "email" ||
        prop === "claim_title" ||
        prop === "claim_type" ||
        prop === "claim_level" ||
        prop === "education_category" ||
        prop === "claim_session" ||
        prop === "date" ||
        prop === "decision"
      ) {
        if (product[prop]) {
          flag =
            product[prop]!.toString()
              .toLowerCase()
              .indexOf(text.toLowerCase()) > -1;
        }
      }
      if (flag) break;
    }
    return flag;
  });
};

const defaultReportContent: claimReportContentInterface[] = [
  {
    key: claimReportContentKeyInterface.names,
    title: "Applicant Names",
    status: true,
  },
  {
    key: claimReportContentKeyInterface.national_id,
    title: "National ID",
    status: true,
  },
  { key: claimReportContentKeyInterface.gender, title: "Gender", status: true },
  { key: claimReportContentKeyInterface.phone, title: "Phone", status: true },
  {
    key: claimReportContentKeyInterface.claim_session,
    title: "Claim Session",
    status: true,
  },
  {
    key: claimReportContentKeyInterface.education_category,
    title: "Education Institute",
    status: true,
  },
  {
    key: claimReportContentKeyInterface.date,
    title: "Claim Date",
    status: true,
  },
  {
    key: claimReportContentKeyInterface.decision,
    title: "Decision",
    status: true,
  },
  {
    key: claimReportContentKeyInterface.claim_title,
    title: "Claim Title",
    status: true,
  },
  {
    key: claimReportContentKeyInterface.claim_type,
    title: "Claim Type",
    status: true,
  },
  {
    key: claimReportContentKeyInterface.claim_level,
    title: "Claim Level",
    status: true,
  },
];

type AppProps = {
  auth: Auth;
  application_sessions: ApplicationSessionInterface[];
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  userList: UserInterface[];
  systemPositions: PositionAPI[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  setSelectedClaimType: Function;
  getPositions: Function;
  getAllClaims: Function;
  GetClaimingTypes: Function;
  getAllApplications: Function;
  getApplicationSessions: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  fetchUserAccess: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

const _ClaimingReport: React.FC<AppProps> = (props) => {
  const [thisPeriod, setThisPeriod] =
    useState<ApplicationSessionInterface | null>(null);
  const [thisLevel, setThisLevel] = useState<number | "all">("all");
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingSession, setLoadingSession] = useState<boolean>(true);
  const [periodChanged, setPeriodChanged] = useState<boolean>(false);
  const [loadingPosition, setLoadingPosition] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] = useState<boolean>(true);
  const [loadingClaims, setLoadingClaims] = useState<boolean>(true);
  const [loadingDocuments, setLoadingDocuments] = useState<boolean>(true);
  const [loadingInstitutes, setLoadingInstitutes] = useState<boolean>(true);
  const [loadingClarifications, setLoadingClarifications] =
    useState<boolean>(true);
  const [loadingUsers, setLoadingUsers] = useState(true);
  const [loadingFields, setLoadingFields] = useState<boolean>(true);
  const [claimToPrint, setClaimToPrint] = useState<
    ClaimToPrintInterface[] | null
  >();

  const [sortProperty, setSortProperty] = useState<string>("");
  const [isSorted, setIsSorted] = useState<boolean>(false);
  const [sortOrder, setSortOrder] = useState<"ASC" | "DESC">("ASC");
  const [searchInput, setSearchInput] = useState<string>("");
  const [generateReportModal, setGenerateReportModal] = useState<{
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null>(null);
  const [reportTitle, setReportTitle] = useState<string>("");
  const [reportContent, setReportContent] =
    useState<claimReportContentInterface[]>(defaultReportContent);
  const [selectAll, setSelectAll] = useState<boolean>(true);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [selectPeriod, setSelectPeriod] = useState<boolean>(false);
  const [thisEducation, setThisEducation] = useState<
    education_category | "all"
  >("all");

  useEffect(() => {
    // LOADING EDUCATION CATEGORY =================================
    if (
      props.schoolLevels.education_categories === null ||
      props.schoolLevels.education_categories.length === 0
    ) {
      props.loadEducationCategories(setLoadingInstitutes);
    } else {
      setLoadingInstitutes(false);
    }
    // END LOADING EDUCATION CATEGORY =================================
    // LOADING CLAIM TYPES ========================================
    if (props.claiming.claimTypes === null) {
      props.GetClaimingTypes((status: boolean, msg: string) => {
        if (status === false) {
          if (msg.length > 0) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }
    // END LOADING CLAIM TYPES ========================================
    // LOADING APPLICATION SESSION ====================================
    if (!props.application_sessions.length && loadingSession) {
      props.getApplicationSessions((status: boolean, msg: string) => {
        if (!status) {
          setLoadingSession(false);
          setLoading(false);
          if (msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    } else if (loadingSession) {
      setLoading(false);
      setLoadingSession(false);
    }

    // SETTING ACTIVE SESSION ==========================
    if (thisPeriod === null && !loadingSession) {
      let thisSession = props.application_sessions.find(
        (item) => item.is_active === true
      );
      setThisPeriod(
        thisSession
          ? thisSession
          : props.application_sessions[props.application_sessions.length - 1]
      );
    }

    // LOADING DOCUMENT CATEGORIES ==========================
    if (!props.documentReducer.documentCategory && loadingDocuments) {
      props.getDocumentCategory();
      setLoadingDocuments(false);
    } else if (loadingDocuments) {
      setLoadingDocuments(false);
    }
    // LOADING DOCUMENT CLARIFICATIONS ==========================
    if (!props.documentReducer.documentSubCategory && loadingClarifications) {
      props.getDocumentSubCategory();
      setLoadingClarifications(false);
    } else if (loadingClarifications) {
      setLoadingClarifications(false);
    }
    // LOADING DOCUMENT FIELDS ==========================
    if (!props.documentReducer.documentFields && loadingFields) {
      props.getDocumentFields();
      setLoadingFields(false);
    } else if (loadingFields) {
      setLoadingFields(false);
    }

    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (props.claiming.allClaims === null && thisPeriod !== null) {
      props.getAllClaims(
        2,
        thisPeriod.application_session_id,
        (status: boolean, msg: string) => {
          !status && setLoadingClaims(status);
          if (status === false && msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (thisPeriod && loadingClaims) {
      setLoadingClaims(false);
    }

    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (props.claiming.allApplications === null && thisPeriod !== null) {
      props.getAllApplications(
        thisPeriod.application_session_id,
        (status: boolean, msg: string) => {
          !status && setLoadingApplications(status);
          if (status === false && msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (thisPeriod && loadingApplications) {
      setLoadingApplications(false);
    }

    if (!props.userList.length && loadingUsers) {
      setLoadingUsers(false);
      props.fetchUserAccess();
    } else if (loadingUsers) {
      setLoadingUsers(false);
    }

    if (props.systemPositions === null) {
      props.getPositions((status: boolean, msg: string) => {
        setLoadingPosition(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (loadingPosition) {
      setLoadingPosition(false);
    }
  }, [
    loadingApplications,
    loadingClaims,
    loadingClarifications,
    loadingDocuments,
    loadingFields,
    loadingPosition,
    loadingSession,
    loadingUsers,
    props,
    thisPeriod,
  ]);

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }

  const displayClaimName = (
    claiming: ClaimingReducerType,
    thisClaim: CandidateClaimInterface
  ): UserInterface | null => {
    let result: UserInterface | null = null;
    let thisClaimType: ClaimTypesInterfaceAPI | undefined = whichClaimType(
      claiming.claimTypes!,
      thisClaim
    );
    if (thisClaimType) {
      if (
        thisClaimType.title.toLocaleLowerCase() ===
        ClaimTypesInterface.DOCUMENT.toLocaleLowerCase()
      ) {
        result = getUserDocuments(props.userList, thisClaim.user)
          ? getUserDocuments(props.userList, thisClaim.user)!.user
          : null;
      }
      if (
        thisClaimType.title.toLocaleLowerCase() ===
        ClaimTypesInterface.APPLICATION.toLocaleLowerCase()
      ) {
        if (props.claiming.allApplications) {
          result = getUserDocuments(props.userList, thisClaim.user)
            ? getUserDocuments(props.userList, thisClaim.user)!.user
            : null;
        }
      }
    }

    return result;
  };

  const displayClaimTitle = (
    claiming: ClaimingReducerType,
    thisClaim: CandidateClaimInterface
  ): string => {
    let result: string = thisClaim.claiming_on_id;
    let thisClaimType: ClaimTypesInterfaceAPI | undefined = whichClaimType(
      claiming.claimTypes!,
      thisClaim
    );
    if (thisClaimType) {
      if (
        thisClaimType.title.toLocaleLowerCase() ===
        ClaimTypesInterface.DOCUMENT.toLocaleLowerCase()
      ) {
        result = getUserDocuments(props.userList, thisClaim.user)
          ? adminDisplayClaimTitle(
              props.documentReducer,
              props.claiming.claimTypes!,
              getUserDocuments(props.userList, thisClaim.user)!.document,
              thisClaim
            )
          : "Document not found";
      }
      if (
        thisClaimType.title.toLocaleLowerCase() ===
        ClaimTypesInterface.APPLICATION.toLocaleLowerCase()
      ) {
        if (props.claiming.allApplications) {
          let thisApplication = props.claiming.allApplications.find(
            (item) => item._id === thisClaim.claiming_on_id
          );
          if (
            thisApplication &&
            props.claiming.allApplications &&
            props.systemPositions
          ) {
            let thisPosition = props.systemPositions.find(
              (item) => item.position_id === thisApplication!.position
            );
            if (thisPosition) {
              result = thisPosition.position_title;
            } else {
              result = "Application not found";
            }
          }
        }
      }
    }

    return result;
  };

  const getClaimInstitute = (
    claiming: ClaimingReducerType,
    thisClaim: CandidateClaimInterface
  ): string | null => {
    let thisClaimType: ClaimTypesInterfaceAPI | undefined = whichClaimType(
      claiming.claimTypes!,
      thisClaim
    );
    if (thisClaimType) {
      if (
        thisClaimType.title.toLocaleLowerCase() ===
        ClaimTypesInterface.DOCUMENT.toLocaleLowerCase()
      ) {
        return null;
      }
      if (
        thisClaimType.title.toLocaleLowerCase() ===
        ClaimTypesInterface.APPLICATION.toLocaleLowerCase()
      ) {
        if (props.claiming.allApplications) {
          let thisApplication = props.claiming.allApplications.find(
            (item) => item._id === thisClaim.claiming_on_id
          );
          if (
            thisApplication &&
            props.claiming.allApplications &&
            props.systemPositions
          ) {
            // return thisApplication.
            let thisPosition = props.systemPositions.find(
              (item) => item.position_id === thisApplication!.position
            );
            if (thisPosition) {
              return thisPosition.education_institute_id.education_institute_id;
            }
          }
        }
      }
    }

    return null;
  };

  const onPeriodChange = (period_id: string) => {
    setPeriodChanged(true);
    props.getAllClaims(2, period_id, (status: boolean, msg: string) => {
      !status && setPeriodChanged(false);
      if (status === false && msg.length) {
        props.PushPopupAlert({
          title: msg,
          theme: PopupAlertTheme.danger,
        });
      }
    });
  };

  const isClaimTypeActive = (
    selectedType: ClaimTypesInterfaceAPI | "all",
    currentType: ClaimTypesInterfaceAPI | "all"
  ): boolean => {
    if (currentType === "all" && selectedType === "all") {
      return true;
    } else if (currentType !== "all" && selectedType !== "all") {
      if (currentType.id === selectedType.id) {
        return true;
      }
    }
    return false;
  };

  const setSort = (thisProperty: string) => {
    if (sortProperty === thisProperty && sortOrder === "ASC") {
      setSortOrder("DESC");
    } else {
      setSortOrder("ASC");
    }
    setSortProperty(thisProperty);
    setIsSorted(true);
  };

  const ClaimTypes = (
    claims: ClaimToPrintInterface[],
    claimsTypes: ClaimTypesInterfaceAPI[]
  ): {
    title: string;
    count: number;
    type: ClaimTypesInterfaceAPI | "all";
  }[] => {
    let output: {
      title: string;
      count: number;
      type: ClaimTypesInterfaceAPI | "all";
    }[] = [{ title: "All types", count: claims.length, type: "all" }];
    for (let i in claimsTypes) {
      output.push({
        title: claimsTypes[i].title,
        count: claims.filter((itm) => itm.claim_type === claimsTypes[i].title)
          .length,
        type: claimsTypes[i],
      });
    }
    return output;
  };

  const ClaimLevels = (
    claims: ClaimToPrintInterface[]
  ): {
    title: string;
    count: number;
    level: number | "all";
  }[] => {
    let output: {
        title: string;
        count: number;
        level: number | "all";
      }[] = [{ title: "All levels", count: claims.length, level: "all" }],
      levels: number[] = [1, 2];
    for (let i in levels) {
      output.push({
        title: "Level " + levels[i],
        count: claims.filter((itm) => itm.claim_level === levels[i]).length,
        level: levels[i],
      });
    }
    return output;
  };

  const updateReportContent = (key: claimReportContentKeyInterface) => {
    let index: number = reportContent.findIndex((item) => item.key === key);
    let newArray: claimReportContentInterface[] = reportContent;
    newArray[index].status = !newArray[index].status;
    setReportContent(newArray);
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      setSelectAll(false);
    } else {
      setSelectAll(true);
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    updateClaimToPrint();
  };

  const updateClaimToPrint = () => {
    // UPDATE REPORT TITLE ========================
    if (thisPeriod) {
      let title = "Claim Report " + thisPeriod.application_session_name;
      // if (thisLevel!=="all") {
      //   title = "Claim Report - Level "+thisLevel + thisPeriod.application_session_name;
      // }
      setReportTitle(title);
    }
    // UPDATE CONTENT =============================
    if (props.claiming.allClaims && props.claiming.claimTypes) {
      let arrayNew: ClaimToPrintInterface[] = [];
      let n: number = 1;
      for (const key in props.claiming.allClaims) {
        if (
          Object.prototype.hasOwnProperty.call(props.claiming.allClaims, key)
        ) {
          for (const keyClaim in props.claiming.allClaims[key].claims) {
            if (
              Object.prototype.hasOwnProperty.call(
                props.claiming.allClaims[key].claims,
                keyClaim
              )
            ) {
              const element = props.claiming.allClaims[key].claims[keyClaim];
              const thisUser = displayClaimName(
                props.claiming,
                props.claiming.allClaims[key]
              );
              const thisType = whichClaimType(
                props.claiming.claimTypes,
                props.claiming.allClaims[key]
              );
              let dataArray: ClaimToPrintInterface = {
                claim_id: n,
                names: thisUser
                  ? thisUser!.user_info.fname + " " + thisUser!.user_info.lname
                  : "",
                gender: thisUser
                  ? thisUser!.user_info.sex
                    ? "Male"
                    : "Female"
                  : "",
                national_id: thisUser ? thisUser!.n_id! : "",
                phone: thisUser ? thisUser!.phone_number : "",
                email: thisUser ? thisUser!.email : "",
                claim_type: thisType ? thisType.title : "",
                claim_level: element.level,
                education_category: "",
                claim_session: thisPeriod
                  ? thisPeriod.application_session_name
                  : "",
                date: DATE(element.createdAt),
                decision: element.decision_taken_status
                  ? element.decision_taken_status
                  : "PENDING",
                claim_title: displayClaimTitle(
                  props.claiming,
                  props.claiming.allClaims[key]
                ),
              };
              for (const key in reportContent) {
                if (Object.prototype.hasOwnProperty.call(reportContent, key)) {
                  if (!reportContent[key].status) {
                    delete dataArray[reportContent[key].key];
                  }
                }
              }
              arrayNew.push(dataArray);
              n++;
            }
          }
        }
      }
      setClaimToPrint(arrayNew);
      arrayNew = [];
      n = 1;
    }
  };

  const FC_ClaimData = (
    allClaims: CandidateClaimInterface[] | null,
    claimTypes: ClaimTypesInterfaceAPI[] | null
  ): ClaimToPrintInterface[] | null => {
    if (allClaims === null || claimTypes === null) return null;
    let arrayNew: ClaimToPrintInterface[] = [];
    let n: number = 1;
    // LOOP IN ALL CLAIM ONE BY ONE ###########################
    for (const key in allClaims) {
      if (Object.prototype.hasOwnProperty.call(allClaims, key)) {
        // LOOP IN ALL CLAIM OF ONE FILE ONE BY ONE ===========================
        for (const keyClaim in allClaims[key].claims) {
          if (
            Object.prototype.hasOwnProperty.call(
              allClaims[key].claims,
              keyClaim
            )
          ) {
            const element = allClaims[key].claims[keyClaim];
            const thisUser = displayClaimName(props.claiming, allClaims[key]);
            const thisType = whichClaimType(claimTypes, allClaims[key]);
            let dataArray: ClaimToPrintInterface = {
              claim_id: n,
              names: thisUser
                ? thisUser.user_info.fname + " " + thisUser.user_info.lname
                : "",
              gender: thisUser
                ? thisUser!.user_info.sex
                  ? "Male"
                  : "Female"
                : "",
              national_id: thisUser ? thisUser!.n_id! : "",
              phone: thisUser ? thisUser!.phone_number : "",
              email: thisUser ? thisUser!.email : "",
              claim_type: thisType ? thisType.title : "",
              claim_level: element.level,
              education_category: getClaimInstitute(
                props.claiming,
                allClaims[key]
              ),
              claim_session: thisPeriod
                ? thisPeriod.application_session_name
                : "",
              date: DATE(element.createdAt),
              decision: element.decision_taken_status
                ? element.decision_taken_status
                : "PENDING",
              claim_title: displayClaimTitle(props.claiming, allClaims[key]),
            };
            for (const key in reportContent) {
              if (Object.prototype.hasOwnProperty.call(reportContent, key)) {
                if (!reportContent[key].status) {
                  delete dataArray[reportContent[key].key];
                }
              }
            }
            arrayNew.push(dataArray);
            n++;
          }
        }
      }
    }
    return arrayNew;
  };

  const resetReportContent = (currentState: boolean) => {
    let newArray: claimReportContentInterface[] = reportContent;
    for (const key in newArray) {
      if (isChangeAllowed(newArray[key].key)) {
        newArray[key].status = !currentState;
      }
    }

    setReportContent(newArray);
    setSelectAll(!currentState);
    updateClaimToPrint();
  };

  const isChangeAllowed = (key: claimReportContentKeyInterface): boolean => {
    if (
      key === claimReportContentKeyInterface.names ||
      key === claimReportContentKeyInterface.decision ||
      key === claimReportContentKeyInterface.claim_session ||
      key === claimReportContentKeyInterface.claim_type ||
      key === claimReportContentKeyInterface.date
    ) {
      return false;
    }
    return true;
  };

  const FC_GeneratePDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return false;
    }

    if (thisPeriod) {
      const table: HTMLElement | null =
        document.getElementById("report_content");
      const reportSettings: reportSettingsInterface = {
        doneBy: props.auth.user.fname + " " + props.auth.user.lname,
        title: "Claim Report",
        subjectTitle: "Claim type",
        subject:
          props.claiming.selectedClaimType === "all"
            ? "All Types"
            : props.claiming.selectedClaimType.title,
        period: thisPeriod.application_session_name,
        footer: "Online Recruitment",
        institute:
          thisEducation === "all"
            ? "All institute"
            : thisEducation.education_category_title,
      };
      table && generatePDF(reportSettings, table);
    }
  };

  const FC_FilterClaims = (
    allClaims: ClaimToPrintInterface[],
    claimType: ClaimTypesInterfaceAPI | "all",
    claimLevel: number | "all",
    institute: education_category | "all"
  ): ClaimToPrintInterface[] => {
    let result: ClaimToPrintInterface[] = allClaims;
    if (claimType !== "all") {
      result = result.filter((item) => item.claim_type === claimType.title);
    }
    if (claimLevel !== "all") {
      result = result.filter((item) => item.claim_level === claimLevel);
    }

    if (institute !== "all") {
      result = result.filter(
        (item) =>
          item.education_category === null ||
          item.education_category === institute.education_institute_id
      );
    }

    return result;
  };

  return (
    <Fragment>
      <SideNavigation>
        <div className="flex flex-col mb-4">
          <span className="text-sm text-gray-600 mb-2">
            Education categories:
          </span>
          {props.schoolLevels.education_categories === null ||
          loadingInstitutes ? (
            <Loading />
          ) : props.schoolLevels.education_categories.length === 0 ? (
            <span>No result found</span>
          ) : (
            <Fragment>
              <div
                onClick={() => setThisEducation("all")}
                className={`border rounded px-2 py-2 mb-2 cursor-pointer ${
                  thisEducation === "all"
                    ? "bg-blue-600 text-white hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                    : "hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                } font-bold flex flex-row items-center gap-2`}
              >
                {thisEducation === "all" ? (
                  <MdRadioButtonChecked />
                ) : (
                  <MdRadioButtonUnchecked />
                )}
                <span>All</span>
              </div>
              {props.schoolLevels.education_categories.map((item, i) => (
                <div
                  onClick={() => setThisEducation(item)}
                  key={i + 1}
                  className={`border rounded px-2 py-2 mb-2 cursor-pointer ${
                    thisEducation !== "all" &&
                    thisEducation.education_institute_id ===
                      item.education_institute_id
                      ? "bg-blue-600 text-white hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                      : "hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                  } font-bold flex flex-row items-center gap-2`}
                >
                  {thisEducation !== "all" &&
                  thisEducation.education_institute_id ===
                    item.education_institute_id ? (
                    <MdRadioButtonChecked />
                  ) : (
                    <MdRadioButtonUnchecked />
                  )}
                  <span>{item.education_category_title}</span>
                </div>
              ))}
            </Fragment>
          )}
        </div>
        <div className="">
          <div className="text-gray-600 text-sm mb-2">Selected period:</div>
          <div
            onClick={() => setSelectPeriod(true)}
            className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline  text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
          >
            {thisPeriod !== null
              ? `${thisPeriod.application_session_name} - ${thisPeriod.period}`
              : "Filter by period"}
            <FiEdit className="text-3xl text-blue-600" />
          </div>
        </div>
        <div className="pt-1">
          <div className="text-sm text-gray-700 pb-2 pt-2">Claim types:</div>
          {props.claiming.claimTypes === null ||
          FC_ClaimData(props.claiming.allClaims, props.claiming.claimTypes) ===
            null ? (
            <Loading />
          ) : (
            ClaimTypes(
              FC_ClaimData(
                props.claiming.allClaims,
                props.claiming.claimTypes
              )!,
              props.claiming.claimTypes
            ).map((item, index: number) => (
              <div
                onClick={() => props.setSelectedClaimType(item.type)}
                key={item.title + index}
              >
                <MainItem
                  title={item.title}
                  count={item.count}
                  active={isClaimTypeActive(
                    props.claiming.selectedClaimType,
                    item.type
                  )}
                />
              </div>
            ))
          )}
        </div>
        <div className="pt-1">
          <div className="text-sm text-gray-700 pb-2 pt-2">Claim level:</div>
          {props.claiming.claimTypes === null ||
          FC_ClaimData(props.claiming.allClaims, props.claiming.claimTypes) ===
            null ? (
            <Loading />
          ) : (
            ClaimLevels(
              FC_ClaimData(props.claiming.allClaims, props.claiming.claimTypes)!
            ).map((item, index: number) => (
              <div
                onClick={() => setThisLevel(item.level)}
                key={item.title + index}
              >
                <MainItem
                  title={item.title}
                  count={item.count}
                  active={item.level === thisLevel}
                />
              </div>
            ))
          )}
        </div>
      </SideNavigation>
      <section>
        {/* REPORT TITLE ============================================ */}
        <div className="grid md:grid-cols-12">
          <div className="col-span-6">
            <div className="mt-4 mx-4 flex flex-col gap-1">
              <span className="text-3xl font-bold text-gray-700">
                Claim report
              </span>
            </div>
          </div>
          <div className="col-span-6">
            <div className="float-right mt-2">
              {props.claiming.allClaims && props.claiming.allClaims.length > 0 && (
                <div className="flex flex-row items-center justify-end font-bold mb-2">
                  <BiExport className="text-2xl text-gray-500" />
                  <span className="text-gray-500">Export as</span>
                  <button
                    className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                    disabled={loading}
                    onClick={() => {
                      updateClaimToPrint();
                      setGenerateReportModal({
                        reportType: "EXCEL",
                        status: true,
                      });
                    }}
                  >
                    EXCEL
                  </button>
                  <button
                    className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                    onClick={() => FC_GeneratePDF()}
                    disabled={loading}
                  >
                    PDF
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
        {/* END REPORT TITLE ============================================ */}
        <div className="bg-white my-4">
          <div className="w-full px-2 py-2">
            <input
              onChange={(e) => setSearchInput(e.target.value)}
              type="text"
              value={searchInput}
              placeholder="Search..."
              className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
              disabled={
                props.claiming.allClaims === null ||
                props.claiming.claimTypes === null ||
                FC_ClaimData(
                  props.claiming.allClaims,
                  props.claiming.claimTypes
                ) === null ||
                periodChanged
              }
            />
          </div>
          <table className="w-full shadow" id="report_content">
            <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
              <tr>
                <td className="px-2 py-2">#</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("names")}
                >
                  <div className="flex items-center gap-1">
                    Applicant names <BiSort />
                  </div>
                </td>
                <td className="px-2 py-2">National ID</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("gender")}
                >
                  <div className="flex items-center gap-1">
                    Gender <BiSort />
                  </div>
                </td>
                <td className="px-2 py-2">Phone</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("claim_title")}
                >
                  <div className="flex items-center gap-1">
                    Claim Title <BiSort />
                  </div>
                </td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("claim_type")}
                >
                  <div className="flex items-center gap-1">
                    Claim Type <BiSort />
                  </div>
                </td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("claim_level")}
                >
                  <div className="flex items-center gap-1">
                    Claim Level <BiSort />
                  </div>
                </td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("decision")}
                >
                  <div className="flex items-center gap-1">
                    Status <BiSort />
                  </div>
                </td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("date")}
                >
                  <div className="flex items-center gap-1">
                    Date <BiSort />
                  </div>
                </td>
              </tr>
            </thead>
            <tbody>
              {props.claiming.allClaims === null ||
              props.claiming.claimTypes === null ||
              periodChanged ? (
                <TableLoading cols={10} />
              ) : FC_ClaimData(
                  props.claiming.allClaims,
                  props.claiming.claimTypes
                ) === null ? (
                <TableLoading cols={10} />
              ) : FC_FilterClaims(
                  FC_ClaimData(
                    props.claiming.allClaims,
                    props.claiming.claimTypes
                  )!,
                  props.claiming.selectedClaimType,
                  thisLevel,
                  thisEducation
                ).length === 0 ? (
                <tr>
                  <td colSpan={10} className="px-2 py-2">
                    <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                      <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                      <span className="text-sm font-bold">
                        No result found! Please try to search again
                      </span>
                      <div className="w-full mt-2 flex flex-row items-center justify-center">
                        <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                          <li
                            onClick={() => setSelectPeriod(true)}
                            className="underline text-gray-500 cursor-pointer"
                          >
                            Filter by period
                          </li>
                          <li className="text-gray-400 italic">or</li>
                          <li className="text-gray-500">
                            Filter by Claim type
                          </li>
                          <li className="text-gray-400 italic">or</li>
                          <li className="text-gray-500">
                            Filter by Claim Level
                          </li>
                        </ul>
                      </div>
                    </div>
                  </td>
                </tr>
              ) : (
                sortThisArray(
                  isSorted,
                  searchData(
                    FC_FilterClaims(
                      FC_ClaimData(
                        props.claiming.allClaims,
                        props.claiming.claimTypes
                      )!,
                      props.claiming.selectedClaimType,
                      thisLevel,
                      thisEducation
                    ),
                    searchInput
                  ),
                  sortProperty,
                  sortOrder
                ).map((item, i) => (
                  <tr
                    key={i + 1}
                    className={`${
                      i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                    } text-gray-600 hover:bg-blue-100 hover:text-blue-700 cursor-pointer border-b border-gray-300 hover:border-blue-500 text-sm`}
                  >
                    <td className="px-2 py-2">{i + 1}</td>
                    <td className="px-2 py-2">{item.names}</td>
                    <td className="px-2 py-2">{item.national_id}</td>
                    <td className="px-2 py-2">{item.gender}</td>
                    <td className="px-2 py-2">{item.phone}</td>
                    <td className="px-2 py-2">{item.claim_title}</td>
                    <td className="px-2 py-2">{item.claim_type}</td>
                    <td className="px-2 py-2">{"Level " + item.claim_level}</td>
                    <td className="px-2 py-2">
                      {item.decision}
                      {/* {capitalizeFirstLetter(item.decision)} */}
                    </td>
                    <td className="px-2 py-2">{item.date}</td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </section>
      {/* GENERATE DYNAMIC REPORT CONTENT MODAL */}
      {generateReportModal !== null && generateReportModal.status === true && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
              <div className="flex flex-col">
                <span className="font-bold text-2xl text-gray-500">
                  Generating Report contents
                </span>
                <span className="text-sm text-blue-600 font-normal">
                  Generating {generateReportModal.reportType} Report
                </span>
              </div>
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => setGenerateReportModal(null)}
          backDropClose={false}
          widthSizeClass={ModalSize.medium}
          displayClose={true}
          padding={{ title: true }}
        >
          <div className="px-6">
            <div className="my-2 text-base text-gray-700 sm:text-lg">
              <div className="flex flex-col">
                <label>Title</label>
                <input
                  type="text"
                  className={`text-left input-white w-20 inline-block ${
                    formError && formError.element === "report_title"
                      ? "border-red-500"
                      : "border-gray-500"
                  } border-2`}
                  placeholder="Sub Category Title"
                  value={reportTitle}
                  onChange={(e) => {
                    setFormError(null);
                    setReportTitle(e.target.value);
                  }}
                  disabled={loading}
                />
                {formError && formError.element === "report_title" && (
                  <span className="text-red-500">{formError.msg}</span>
                )}
              </div>
              <div className="flex flex-col mt-2">
                <label>Report content</label>
                <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                  {reportContent.map((item, index) => (
                    <div
                      key={index}
                      className={`flex items-center gap-1 ${
                        isChangeAllowed(item.key)
                          ? "cursor-pointer"
                          : "cursor-not-allowed"
                      }`}
                      onClick={() =>
                        isChangeAllowed(item.key) &&
                        updateReportContent(item.key)
                      }
                      title={
                        !isChangeAllowed(item.key)
                          ? "Required for every report!"
                          : ""
                      }
                    >
                      {item.status ? (
                        <MdCheckBox className="w-6 h-6 text-blue-700" />
                      ) : (
                        <MdCheckBoxOutlineBlank className="w-6 h-6" />
                      )}
                      <span>{item.title}</span>
                    </div>
                  ))}
                </div>
                {formError && formError.element === "report_content" && (
                  <span className="text-red-500">{formError.msg}</span>
                )}
              </div>
            </div>
            <div className="flex justify-between mb-2 mt-4">
              <div
                className={`flex items-center gap-1 cursor-pointer`}
                onClick={() => resetReportContent(selectAll)}
              >
                {selectAll ? (
                  <MdCheckBox className="w-6 h-6 text-blue-700" />
                ) : (
                  <MdCheckBoxOutlineBlank className="w-6 h-6" />
                )}
                <span>Select all</span>
              </div>

              {claimToPrint && !loading && (
                <ExportToExcel
                  fileData={claimToPrint}
                  fileName={reportTitle}
                  btnName="Generate"
                />
              )}
            </div>
          </div>
        </Modal>
      )}
      {/* END GENERATE DYNAMIC REPORT CONTENT MODAL */}
      {selectPeriod && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              <MdHistory className="text-5xl text-blue-600" />
              <span className="flex flex-col">Choose a period</span>
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => thisPeriod !== null && setSelectPeriod(false)}
          backDropClose={true}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          padding={{ title: true }}
        >
          <div className="px-2 pb-2">
            {loadingSession ? (
              <Loading />
            ) : props.application_sessions.length === 0 ? (
              <div className="flex flex-col text-gray-400 items-center justify-center">
                <MdSearch className="text-6xl" />
                <div className="flex flex-col items-center justify-center">
                  <span>No application periods found!</span>
                </div>
              </div>
            ) : (
              props.application_sessions.map((item, i) => (
                <div
                  onClick={() => {
                    setThisPeriod(item);
                    onPeriodChange(item.application_session_id);
                    setSelectPeriod(false);
                  }}
                  key={i + 1}
                  className="grid grid-cols-12 bg-blue-50 border border-blue-200 rounded px-2 py-2 cursor-pointer hover:bg-blue-100 hover:border-blue-500 mb-3"
                >
                  <div className="col-span-5">
                    <div className="text-blue-500 text-sm italic">Period</div>
                    <div className="font-bold">
                      {item.application_session_name}
                    </div>
                    <div className="my-4"></div>
                    <div className="text-blue-500 text-sm italic">Status</div>
                    <div className="font-bold">
                      {item.is_active === true ? "Active" : "Expired"}
                    </div>
                  </div>
                  <div className="col-span-5">
                    <div className="text-blue-500 text-sm italic">
                      Start date
                    </div>
                    <div className="font-bold">
                      {item.application_start_date}
                    </div>
                    <div className="my-4"></div>
                    <div className="text-blue-500 text-sm italic">
                      Ending date
                    </div>
                    <div className="font-bold">{item.application_end_date}</div>
                  </div>
                  <div className="col-span-2 flex items-center justify-end">
                    <div className="btn bg-blue-600 text-white mr-2">
                      Choose
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </Modal>
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
  claiming,
  document,
  users,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  application_sessions: ApplicationSessionInterface[];
  systemPositions: PositionAPI[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  userList: UserInterface[];
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return {
    auth: auth,
    application_sessions: system_settings.applicationSessions,
    systemPositions: system_settings.systemPositions,
    claiming,
    documentReducer: document,
    userList: users,
    schoolLevels,
  };
};

export const ClaimingReport = connect(mapStateToProps, {
  getAllClaims,
  GetClaimingTypes,
  getAllApplications,
  getApplicationSessions,
  loadEducationCategories,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  setSelectedClaimType,
  fetchUserAccess,
  getPositions,
  PushPopupAlert,
})(_ClaimingReport);
