import axios from "axios";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../../actions";
import { Auth } from "../../../actions/auth";
import {
  createClaim,
  DocumentClaimUser,
  GetClaimingTypes,
  getMyRejectedApplications,
  getMyRejectedDocuments,
} from "../../../actions/claiming";
import {
  getDocumentCategory,
  getDocumentFields,
  getDocumentSubCategory,
} from "../../../actions/document";
import { getApplicationSessions } from "../../../actions/settings";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import {
  CandidateClaimInterface,
  claimApplicationsInterface,
  ClaimTypesInterface,
} from "../../../interfaces";
import { ApplicationStates } from "../../../interfaces/application";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { ClaimingReducerType } from "../../../reducers/claiming";
import { DocumentReducerInterface } from "../../../reducers/document";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import { ApplicationSessionInterface } from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { setAxiosToken } from "../../../utils/AxiosToken";
import { errorToText } from "../../../utils/errors";
import ApplicationFormView from "./ApplicationFormView";
import DocFormView from "./DocFormView";
import FormApplicationList from "./FormApplicationList";
import FormDocumentList from "./FormDocumentList";

export const ClaimGetDocTitle = (
  documentCategories: DocumentReducerInterface,
  doc: DocumentClaimUser
): { category: string; sub_category: string; field: string; main: string } => {
  let DocCategory: string = "",
    DocSubCategory: string = "",
    DocField: string = "",
    DocTitle: string = "";
  if (
    doc.sub_category_id.length > 0 &&
    documentCategories.documentSubCategory
  ) {
    DocTitle = documentCategories.documentSubCategory.filter(
      (c) => c.user_detail_subcategory_id === doc.sub_category_id
    )[0].title;
    DocSubCategory = DocTitle;
  } else if (
    doc.category_id.length > 0 &&
    documentCategories.documentCategory
  ) {
    DocTitle = documentCategories.documentCategory.filter(
      (c) => c.user_detail_category_id === doc.category_id
    )[0].title;
    DocCategory = DocTitle;
  }
  if (
    doc.category_subcategory_data &&
    doc.category_subcategory_data.category_subcategory_Data_id.length > 0 &&
    documentCategories.documentFields
  ) {
    console.log({ doc });
    DocField = documentCategories.documentFields.filter(
      (c) =>
        c._id === doc.category_subcategory_data?.category_subcategory_Data_id
    )[0].title;
    DocTitle += " in " + DocField;
  }

  return {
    category: DocCategory,
    sub_category: DocSubCategory,
    field: DocField,
    main: DocTitle,
  };
};

export const isClaimAllowed = (
  allClaim: CandidateClaimInterface[],
  period_id: string,
  claimOnId: string
): boolean => {
  if (
    allClaim.find(
      (item) =>
        item.period_id === period_id && item.claiming_on_id === claimOnId
    )
  ) {
    return false;
  }
  return true;
};

type ClaimFormProps = {
  // FOR SHORTCUT BUTTONS CALL
  claim_type?: ClaimTypesInterface;
  file_id?: string;
  formState: boolean;
  setFormState: (value: boolean) => void;
  auth: Auth;
  application_sessions: ApplicationSessionInterface[];
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  createClaim: Function;
  getApplicationSessions: Function;
  GetClaimingTypes: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getMyRejectedApplications: Function;
  getMyRejectedDocuments: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

const ClaimForm: React.FC<ClaimFormProps> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingClaimTypes, setLoadingClaimTypes] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] = useState<boolean>(true);
  const [loadingDocuments, setLoadingDocuments] = useState<boolean>(true);
  const [loadingForm, setLoadingForm] = useState<boolean>(false);
  const [loadingSession, setLoadingSession] = useState<boolean>(true);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [thisClaimType, setThisClaimType] = useState<string>("");
  const [thisClaimTypeId, setThisClaimTypeId] = useState<string>("");
  const [claimComment, setClaimComment] = useState<string>("");
  const [claimFileId, setClaimFileId] = useState<string>("");
  const [chooseClaimFile, setChooseClaimFile] = useState<boolean>(false);
  const [viewThisFile, setViewThisFile] = useState<string>("");
  const [formType, setFormType] = useState<"DEFAULT" | "PROPS">("DEFAULT");
  const [alertError, setAlertError] = useState<string | null>(null);
  const [activeSession, setActiveSession] =
    useState<ApplicationSessionInterface | null>(null);

  useEffect(() => {
    const isDocumentIdValid = (
      doc_id: string
    ): {
      status: boolean;
      msg: string;
      alert: PopupAlertTheme;
    } => {
      if (props.claiming.candidateDocuments) {
        let doc = props.claiming.candidateDocuments.find(
          (item) => item.doc_id === doc_id
        );
        if (doc) {
          // CHECK IF DOCUMENT HAS REJECTED STATUS
          if (doc.validated_by.validation_status !== "rejected") {
            return {
              status: false,
              msg: "Only rejected document are allowed for claim",
              alert: PopupAlertTheme.info,
            };
          }
          // CHECK IF DOCUMENT HAS ALREADY CLAIMED ON
          if (
            props.claiming.myClaims &&
            activeSession &&
            isClaimAllowed(
              props.claiming.myClaims,
              activeSession.application_session_id,
              doc_id
            )
          ) {
            return {
              status: false,
              msg: "This document already in claiming list!",
              alert: PopupAlertTheme.info,
            };
          }
          return { status: true, msg: "", alert: PopupAlertTheme.success };
        } else {
          return {
            status: false,
            msg: "Invalid document id",
            alert: PopupAlertTheme.warning,
          };
        }
      }
      return {
        status: false,
        msg: "No Documents, please try again",
        alert: PopupAlertTheme.info,
      };
    };

    const isApplicationIdValid = (
      app_id: string
    ): {
      status: boolean;
      msg: string;
      alert: PopupAlertTheme;
    } => {
      if (props.claiming.candidateApplications) {
        let app = props.claiming.candidateApplications.find(
          (item) => item.application_id === app_id
        );
        if (app) {
          // CHECK IF DOCUMENT HAS REJECTED STATUS
          if (app.application_status !== ApplicationStates.Rejected) {
            return {
              status: false,
              msg: "Only rejected application are allowed for claim",
              alert: PopupAlertTheme.info,
            };
          }
          // CHECK IF DOCUMENT HAS ALREADY CLAIMED ON
          if (
            props.claiming.myClaims &&
            activeSession &&
            isClaimAllowed(
              props.claiming.myClaims,
              activeSession.application_session_id,
              app.application_id
            )
          ) {
            return {
              status: false,
              msg: "This application already in claiming list!",
              alert: PopupAlertTheme.info,
            };
          }
          return { status: true, msg: "", alert: PopupAlertTheme.info };
        } else {
          return {
            status: false,
            msg: "Invalid application id",
            alert: PopupAlertTheme.warning,
          };
        }
      }
      return {
        status: false,
        msg: "No Application, please try again!",
        alert: PopupAlertTheme.info,
      };
    };

    if (!props.documentReducer.documentCategory && loading) {
      props.getDocumentCategory();
    } else if (!props.documentReducer.documentSubCategory && loading) {
      props.getDocumentSubCategory();
    } else if (!props.documentReducer.documentFields && loading) {
      props.getDocumentFields();
    }
    if (
      props.documentReducer.documentFields &&
      props.documentReducer.documentCategory &&
      props.documentReducer.documentSubCategory &&
      loading
    ) {
      setLoading(false);
    }

    if (props.claiming.candidateApplications === null) {
      props.getMyRejectedApplications(
        props.auth.user.user_id,
        (status: boolean, msg: string) => {
          if (status === false) {
            if (msg.length > 0) {
              props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            setLoadingApplications(false);
          }
        }
      );
    } else if (loadingApplications) {
      setLoadingApplications(false);
    }

    if (props.claiming.candidateDocuments === null) {
      props.getMyRejectedDocuments((status: boolean, msg: string) => {
        if (status === false) {
          if (msg.length > 0) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          setLoadingDocuments(false);
        }
      });
    } else {
      setLoadingDocuments(false);
    }

    if (
      props.claiming.claimTypes === null ||
      (props.claiming.claimTypes.length <= 0 && loadingClaimTypes)
    ) {
      props.GetClaimingTypes((status: boolean, msg: string) => {
        if (status === false) {
          if (msg.length > 0) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          setLoadingClaimTypes(false);
        }
      });
    } else {
      setLoadingClaimTypes(false);
    }

    // LOADING SESSIONS ==========================
    if (!props.application_sessions.length && loadingSession) {
      props.getApplicationSessions((status: boolean, msg: string) => {
        setLoadingSession(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (loadingSession) {
      setLoadingSession(false);
    }
    // SETTING ACTIVE SESSION ==========================
    if (activeSession === null && !loadingSession) {
      let thisSession = props.application_sessions.find(
        (item) => item.is_active === true
      );
      setActiveSession(thisSession ? thisSession : null);
    }

    // PROPS VALIDATION PROCESS ###############
    if (props.claim_type && props.file_id && formType === "DEFAULT") {
      if (
        props.claim_type === ClaimTypesInterface.DOCUMENT &&
        !loadingDocuments
      ) {
        let _isDocumentIdValid = isDocumentIdValid(props.file_id);
        if (!_isDocumentIdValid.status) {
          props.PushPopupAlert({
            title: _isDocumentIdValid.msg,
            theme: _isDocumentIdValid.alert,
          });
          // setAlertError(_isDocumentIdValid.msg);
          props.setFormState(false);
        } else {
          if (props.claiming.claimTypes && props.claim_type) {
            let claim = props.claiming.claimTypes.find(
              (item) =>
                item.id === props.claim_type ||
                item.title.toLocaleLowerCase() ===
                  props.claim_type!.toLocaleLowerCase()
            );
            setThisClaimType(claim ? claim.title : "");
            setThisClaimTypeId(claim ? claim.id : "");
          }
          setClaimFileId(props.file_id);
          setFormType("PROPS");
        }
      }
      if (
        props.claim_type === ClaimTypesInterface.APPLICATION &&
        !loadingApplications
      ) {
        let _isApplicationIdValid = isApplicationIdValid(props.file_id);
        if (!_isApplicationIdValid.status) {
          props.PushPopupAlert({
            title: _isApplicationIdValid.msg,
            theme: _isApplicationIdValid.alert,
          });
          props.setFormState(false);
        } else {
          if (props.claiming.claimTypes && props.claim_type) {
            let claim = props.claiming.claimTypes.find(
              (item) =>
                item.id === props.claim_type ||
                item.title.toLocaleLowerCase() ===
                  props.claim_type!.toLocaleLowerCase()
            );
            setThisClaimType(claim ? claim.title : "");
            setThisClaimTypeId(claim ? claim.id : "");
          }
          setClaimFileId(props.file_id);
          setFormType("PROPS");
        }
      }
    }
  }, [
    activeSession,
    formType,
    loading,
    loadingApplications,
    loadingClaimTypes,
    loadingDocuments,
    loadingSession,
    props,
  ]);

  const getDocumentById = (
    documents: DocumentClaimUser[],
    doc_id: string
  ): DocumentClaimUser => {
    return documents.filter((item) => item.doc_id === doc_id)[0];
  };
  const getApplicationById = (
    applications: claimApplicationsInterface[],
    application_id: string
  ): claimApplicationsInterface => {
    return applications.filter(
      (item) => item.application_id === application_id
    )[0];
  };

  const getValidation = (
    thisId: string
  ): {
    old_reason: string;
    old_status: string;
    old_evaluated_by: string;
  } => {
    let result: {
      old_reason: string;
      old_status: string;
      old_evaluated_by: string;
    } = { old_reason: "", old_status: "", old_evaluated_by: "" };

    if (
      thisClaimType === ClaimTypesInterface.DOCUMENT &&
      props.claiming.candidateDocuments
    ) {
      result = {
        old_reason: getDocumentById(props.claiming.candidateDocuments, thisId)
          .validated_by.comment,
        old_status: getDocumentById(props.claiming.candidateDocuments, thisId)
          .validated_by.validation_status,
        old_evaluated_by: getDocumentById(
          props.claiming.candidateDocuments,
          thisId
        ).validated_by.user_id,
      };
    } else if (
      thisClaimType === ClaimTypesInterface.APPLICATION &&
      props.claiming.candidateApplications
    ) {
      result = {
        old_reason: getApplicationById(
          props.claiming.candidateApplications,
          thisId
        ).override_by.comment,
        old_status: getApplicationById(
          props.claiming.candidateApplications,
          thisId
        ).override_by.application_status,
        old_evaluated_by: getApplicationById(
          props.claiming.candidateApplications,
          thisId
        ).override_by.user_id,
      };
    }

    return result;
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (isEmptyOrSpaces(thisClaimTypeId)) {
      return setFormError({
        element: "claim_type",
        msg: "Claim type is required!",
      });
    }
    if (isEmptyOrSpaces(claimFileId)) {
      return setFormError({
        element: "claim_file",
        msg: `Please select ${thisClaimType.toLocaleLowerCase()} you are claiming on!`,
      });
    }

    if (isEmptyOrSpaces(claimComment)) {
      return setFormError({
        element: "claim_comment",
        msg: `Claim comment is required!`,
      });
    }

    if (!activeSession) {
      return setAlertError(`No active claiming session is available!`);
    }

    setLoadingForm(true);
    try {
      setAxiosToken();
      const res = await axios.post<CandidateClaimInterface>(
        `${API.CLAIMING}/user/create`,
        {
          claiming_on_id: claimFileId,
          claim_type: thisClaimTypeId,
          user: props.auth.user.user_id,
          period_id: activeSession.application_session_id,
          reason: claimComment,
          old_reason: getValidation(claimFileId).old_reason,
          old_status: getValidation(claimFileId).old_status,
          old_evaluated_by: getValidation(claimFileId).old_evaluated_by,
        }
      );
      console.log({ res: res.data });
      if (res.status === 201 || res.status === 200) {
        props.createClaim(res.data);
        props.PushPopupAlert({
          title: "Claim sent successful",
          theme: PopupAlertTheme.success,
        });
        cleanToClose();
      }
    } catch (error) {
      console.error({ error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setLoadingForm(false);
  };

  const selectClaimType = (thisClaim: string) => {
    if (props.claiming.claimTypes) {
      let claim = props.claiming.claimTypes.find(
        (item) =>
          item.id === thisClaim ||
          item.title.toLocaleLowerCase() === thisClaim.toLocaleLowerCase()
      );
      setThisClaimType(claim ? claim.title : "");
      setThisClaimTypeId(claim ? claim.id : "");
    }
  };

  const cleanToClose = () => {
    props.setFormState(false);
    setClaimFileId("");
    setThisClaimType("");
    setThisClaimTypeId("");
    setClaimComment("");
    setChooseClaimFile(false);
    setFormType("DEFAULT");
    setViewThisFile("");
    setFormError(null);
  };

  return (
    <div className="pb-3">
      {props.formState && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            !loadingForm && viewThisFile.length <= 0 && cleanToClose();
            !loadingForm && viewThisFile.length > 0 && setViewThisFile("");
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="text-gray-900 font-bold text-xl md:ml-1">
              {viewThisFile.length <= 0 && "Claiming form"}
              {viewThisFile.length > 0 &&
                thisClaimType === ClaimTypesInterface.DOCUMENT &&
                "Viewing document"}
              {viewThisFile.length > 0 &&
                thisClaimType === ClaimTypesInterface.APPLICATION &&
                "Viewing application"}
            </div>
          }
          widthSizeClass={
            viewThisFile.length > 0 ? ModalSize.extraLarge : ModalSize.medium
          }
          padding={{ title: true, body: false, footer: false }}
        >
          {/* CLAIM FORM ============================================== */}
          {viewThisFile.length <= 0 && (
            <form onSubmit={(e) => handleSubmit(e)}>
              <div className="px-6 pb-3">
                {alertError && (
                  <div
                    className="alert-warning cursor-pointer"
                    onClick={() => setAlertError(null)}
                  >
                    {alertError}
                  </div>
                )}
                <div className="text-base text-gray-700 sm:text-lg">
                  {/* SELECT CLAIM TYPE ============================================== */}
                  <div className="flex flex-col mb-2">
                    <label>Claim type</label>
                    <select
                      className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                        formError && formError.element === "claim_type"
                          ? "border-red-500"
                          : "border-gray-500"
                      } ${loadingClaimTypes ? "cursor-wait" : ""} border-2`}
                      onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                        selectClaimType(e.target.value);
                        setClaimFileId("");
                        formError &&
                          formError.element === "claim_type" &&
                          setFormError(null);
                      }}
                      value={thisClaimTypeId}
                      disabled={
                        loadingForm ||
                        props.claiming.claimTypes === null ||
                        formType === "PROPS"
                      }
                    >
                      <option value="">Choose claim type</option>
                      {props.claiming.claimTypes &&
                        props.claiming.claimTypes.map((item, index) => (
                          <option value={item.id} key={index + item.id}>
                            {capitalizeFirstLetter(item.title)}
                          </option>
                        ))}
                    </select>
                    {formError && formError.element === "claim_type" && (
                      <span className="text-red-500">{formError.msg}</span>
                    )}
                  </div>
                  {/* END SELECT CLAIM TYPE ============================================== */}

                  {/* SELECT FILE TO CLAIM ON ============================================== */}
                  <div className="flex flex-col mb-2">
                    <label>
                      Which {thisClaimType.length > 0 ? thisClaimType : "file"}{" "}
                      are you claiming on?
                    </label>
                    <div
                      className={`text-left bg-white border-2 border-gray-700 rounded w-full ${
                        chooseClaimFile ? "h-64" : "h-10"
                      } ${
                        formError && formError.element === "claim_file"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2 ${
                        thisClaimType.length > 0 && !chooseClaimFile
                          ? "cursor-pointer"
                          : !chooseClaimFile && "cursor-not-allowed"
                      }`}
                      title={
                        thisClaimType.length <= 0
                          ? "Choose claim type first!"
                          : ""
                      }
                      onClick={() => {
                        formType === "DEFAULT" &&
                          thisClaimType.length > 0 &&
                          !chooseClaimFile &&
                          setChooseClaimFile(true);
                        formError &&
                          formError.element === "claim_file" &&
                          setFormError(null);
                      }}
                    >
                      {/* DISPLAYING FILTERED SELECTING FILE ============================ */}
                      {claimFileId.length > 0 && !chooseClaimFile ? (
                        <p className="my-1 mx-2">
                          {thisClaimType === ClaimTypesInterface.DOCUMENT &&
                            props.claiming.candidateDocuments &&
                            ClaimGetDocTitle(
                              props.documentReducer,
                              getDocumentById(
                                props.claiming.candidateDocuments,
                                claimFileId
                              )
                            ).main}
                          {thisClaimType === ClaimTypesInterface.APPLICATION &&
                            props.claiming.candidateApplications &&
                            getApplicationById(
                              props.claiming.candidateApplications,
                              claimFileId
                            ).position.position_title}
                        </p>
                      ) : (
                        !chooseClaimFile && (
                          <p className="my-1 mx-2">
                            Select Claim {thisClaimType.toLocaleLowerCase()}
                          </p>
                        )
                      )}
                      {/* END DISPLAYING FILTERED SELECTING FILE ============================ */}

                      {/* LISTING OF REJECTED DOCUMENTS ============================ */}
                      {chooseClaimFile &&
                        thisClaimType === ClaimTypesInterface.DOCUMENT &&
                        props.claiming.candidateDocuments &&
                        activeSession &&
                        props.claiming.myClaims && (
                          <FormDocumentList
                            setChooseClaimFile={setChooseClaimFile}
                            setClaimFileId={setClaimFileId}
                            setViewThisFile={setViewThisFile}
                            viewThisFile={viewThisFile}
                            candidateDocuments={
                              props.claiming.candidateDocuments
                            }
                            documentCategories={props.documentReducer}
                            myClaims={props.claiming.myClaims}
                            activeSession={activeSession}
                          />
                        )}
                      {/* END LISTING OF REJECTED DOCUMENTS ============================ */}
                      {/* LISTING OF REJECTED APPLICATION ============================ */}
                      {chooseClaimFile &&
                        thisClaimType === ClaimTypesInterface.APPLICATION &&
                        props.claiming.candidateApplications &&
                        props.claiming.myClaims &&
                        activeSession && (
                          <FormApplicationList
                            setChooseClaimFile={setChooseClaimFile}
                            setClaimFileId={setClaimFileId}
                            setViewThisFile={setViewThisFile}
                            viewThisFile={viewThisFile}
                            candidateApplications={
                              props.claiming.candidateApplications
                            }
                            myClaims={props.claiming.myClaims}
                            activeSession={activeSession}
                          />
                        )}
                      {/* END LISTING OF REJECTED APPLICATION ============================ */}
                    </div>
                    {formError && formError.element === "claim_file" && (
                      <span className="text-red-500">{formError.msg}</span>
                    )}
                  </div>
                  {/* END SELECT FILE TO CLAIM ON ============================================== */}

                  {/* CLAIM COMMENT ============================================== */}
                  <div className="flex flex-col">
                    <label>Comment</label>
                    <textarea
                      className={`text-left input-white w-20 inline-block ${
                        formError && formError.element === "claim_comment"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder={`Claim comment`}
                      style={{ resize: "none" }}
                      value={claimComment}
                      onChange={(e) => {
                        setClaimComment(e.target.value);
                        formError &&
                          formError.element === "claim_comment" &&
                          setFormError(null);
                      }}
                      rows={3}
                      disabled={loadingForm}
                    ></textarea>
                    {formError && formError.element === "claim_comment" && (
                      <span className="text-red-500">{formError.msg}</span>
                    )}
                  </div>
                  {/* END CLAIM COMMENT ============================================== */}
                </div>
                <div className="pt-3 flex justify-between">
                  <button
                    onClick={() => cleanToClose()}
                    className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
                      loadingForm && "cursor-not-allowed"
                    }`}
                    type="button"
                    disabled={loadingForm}
                  >
                    Exit
                  </button>
                  <button
                    className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                      loadingForm && "cursor-wait"
                    }`}
                    type="submit"
                    disabled={loadingForm}
                  >
                    {loadingForm ? "Loading..." : "Claim"}
                  </button>
                </div>
              </div>
            </form>
          )}
          {/* END CLAIM FORM ============================================== */}

          {/* CLAIM DOCUMENT VIEW ============================================== */}
          {viewThisFile.length > 0 &&
            props.claiming.candidateDocuments &&
            thisClaimType === ClaimTypesInterface.DOCUMENT && (
              <DocFormView
                thisDoc={getDocumentById(
                  props.claiming.candidateDocuments,
                  viewThisFile
                )}
                setViewThisFile={setViewThisFile}
                documentCategories={props.documentReducer}
                setChooseClaimFile={setChooseClaimFile}
                setClaimFileId={setClaimFileId}
              />
            )}
          {/* END CLAIM DOCUMENT VIEW ============================================== */}
          {/* CLAIM APPLICATION VIEW ============================================== */}
          {viewThisFile.length > 0 &&
            props.claiming.candidateApplications &&
            thisClaimType === ClaimTypesInterface.APPLICATION && (
              <ApplicationFormView
                thisApplication={getApplicationById(
                  props.claiming.candidateApplications,
                  viewThisFile
                )}
                documentCategories={props.documentReducer}
                setChooseClaimFile={setChooseClaimFile}
                setClaimFileId={setClaimFileId}
                setViewThisFile={setViewThisFile}
              />
            )}
          {/* END CLAIM APPLICATION VIEW ============================================== */}
        </Modal>
      )}
    </div>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
  document,
  claiming,
}: StoreState): {
  auth: Auth;
  application_sessions: ApplicationSessionInterface[];
  documentReducer: DocumentReducerInterface;
  claiming: ClaimingReducerType;
} => {
  return {
    auth: auth,
    application_sessions: system_settings.applicationSessions,
    documentReducer: document,
    claiming,
  };
};

export default connect(mapStateToProps, {
  createClaim,
  GetClaimingTypes,
  getMyRejectedApplications,
  getMyRejectedDocuments,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  getApplicationSessions,
  PushPopupAlert,
})(ClaimForm);
